.card {
  z-index: 1;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
.card.normal {
  background-color: rgba(255, 255, 255, .9);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
.card.justBorder {
  border: 2px solid rgba(255, 255, 255, .3);
}
.card.borderBlur {
  border: 2px solid rgba(255, 255, 255, .3);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
.card.verticalLine,
.card.horizontalLine {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none !important;
  background: none !important;
}
.card > .line {
  background-color: rgba(255, 255, 255, .3);
  border-radius: 6px;
}
.card.verticalLine > .line {
  width: 2px;
  height: 90%;
}
.card.horizontalLine > .line {
  width: 90%;
  height: 2px;
}
.card.full {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  border: none;
  border-radius: 0;
}
.card.background {
  z-index: 0;
}

.card .title {
  margin: 0;
  padding: 0 1rem;
}

.state {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.state.point {
  left: .5rem;
  top: .5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 1rem;
}
.state.flag {
  left: auto;
  right: -1rem;
  top: -1rem;
  width: 2rem;
  height: 2rem;
  transform: rotate(-45deg);
}
.state.normal {
  background: none;
}
.state.start {
  background: #9fc9ad;
}
.state.doing {
  background: #95bad3;
}
.state.will {
  background: #d3bc95;
}
.state.end {
  background: #f1bdc9;
}
.state.info {
  background: #00aeec;
}
.state.warn {
  background: #ffc300;
}
.state.error {
  background: #fb7299;
}
.state.true {
  background: rgba(255, 255, 255, .9);
}

.max-font-single-line {
  white-space: nowrap;
}